            <div class="input-area">
              <input v-model="password_2" type="password" maxlength="18" placeholder=" " required>
            </div>
            <div class="input-area">
              <input v-model="name" placeholder="" maxlength="5" required>
            </div>
            <div class="input-area">
              <h6></h6>
              <input v-model="resident_1" class="half-input" placeholder=" " maxlength="6" required>-
              <input v-model="resident_2" class="half-input" type="password" maxlength="7" placeholder=" " required>
            </div>
            <transition name="fade" mode="out-in">
              <p class="message-area" v-if="msg">{{ msg }}</p>
            </transition>
            <button :class="done ? 'able' : 'disable'"></button>
          </form>
        </div>
        <div v-else-if="view === 2">
          <h2>{{ msg }}</h2>
          <button class="able" @click="view = 1"></button>
        </div>
        <div v-else-if="view === 3">
          <h2>{{ name }} </h2>
        </div>
      </transition>
      <router-link to="/"> </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'join',
  data () {
    return {
      view: 0,
      agree: false,
      id: '',
      password_1: '',
      password_2: '',
      name: '',
      resident_1: '',
      resident_2: '',
      msg: '',
      alreadySubmit: false
    }
  },
  computed: {
    done () {
      return this.check()
    }
  },
  methods: {
    check () {
      if (!this.id.match(/^[a-z0-9]{6,20}$/)) {
        this.msg = '  ,   6~20 .'
        return false
      }

      if (!this.password_1.match(/^[a-z0-9]{8,18}$/)) {
        this.msg = '  ,   8~18 .'
        return false
      }

      if (this.password_1 !== this.password_2) {
        this.msg = '   '
        return false
      }

      if (!this.name.match(/^[-]{2,5}$/)) {
        this.msg = '   2 .'
        return false
      }

      if (!(this.resident_1.match(/^[0-9]{6}$/) && this.resident_2.match(/^[0-9]{7}$/))) {
        this.msg = '  '
        return false
      }

      this.msg = ''
      return true
    },
    /**
     * @description   
     */
    submit () {
      if (this.alreadySubmit) {
        return
      }
      this.alreadySubmit = true
      this.$http.post('/info/login/regist', {
        id: this.id,
        password: this.password_1,
        name: this.name,
        resident: this.resident_1 + this.resident_2
      }).then(r => {
        console.log(r)
        if (r.data.success) {
          this.msg = this.name + ' '
          this.view = 3
        } else {
          this.msg = r.data.message
          this.view = 2
        }
      }).catch(e => {
        this.view = 2
        console.error(e)
        this.msg = e.message
      }).finally(() => {
        this.alreadySubmit = false
      })
    }
  }
}
</script>

