<template>
  <div id="login">
    <div class="gradient-header"></div>
    <div class="panel">
      <h2></h2>
      <form @submit.prevent="submit">
        <div class="input-area">
          <input v-model="id" placeholder="" @keydown="msg = ''" required>
        </div>
        <div class="input-area">
          <input v-model="password" type="password" autocomplete="password" placeholder="" @keydown="msg = ''" required>
        </div>
        <div class="input-area">
          <button></button>
        </div>
        <div class="login-message">
          <transition name="fade" mode="out-in">
            <p v-if="msg">{{ msg }}</p>
          </transition>
        </div>
      </form>
      <router-link to="/"> </router-link>
      <br><br>
      <b class="ask-account"> ?</b>&nbsp;
      <router-link to="/join"></router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      id: '',
      password: '',
      msg: ''
    }
  },
  methods: {
    /**
     * @description   
     */
    submit () {
      this.$http.post('/login', { id: this.id, password: this.password }).then(r => {
        this.msg = ''
        if (r.data.token) {
          this.$store.commit('SET_TOKEN', r.data.token)
          this.$router.push({ name: 'home' })
        }
      }).catch(e => {
        console.dir(e)
        this.msg = e.message
      })
    }
  }
}
</script>

<style lang="scss">
#login {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.login-message {
  height: 30px;
  color: indianred;
}

.ask-account {
  font-size: 0.9rem;
}
</style>
